* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 1170px;
  max-width: calc(100% - 20px);
  padding: 20px 0;
  margin: 0 auto;
  font-size: 14px;
}

.header_logo {
  width: 128px;
  vertical-align: bottom;
}

.subtitle {
  display: inline-block;
  font-weight: 600;
}

.contact_btn {
  padding: 11px 40px;
  color: #fff;
  text-align: center;
  background-color: #da6b64;
  border-radius: 25px;
}